import React, {PureComponent} from 'react'
import {Form, Button, Row, Col, DatePicker, Input} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxSearch, CsxMultipleSearch, CsxMultipleSelect} from 'Form'
import {API_WDENTRYORDERS_SUPPLIERS} from 'api'
import {PROCUREMENT} from 'Mock'

@connect(
  (state) => ({...state.arrivalPlan, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size, userLoation} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode &&
      (reqObj.splitGroupCode =
        reqObj.splitGroupCode.data &&
        reqObj.splitGroupCode.data.map((o) => o.key)).join(',')
    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }
  // 重置
  reset = () => {
    const {update, userLoation} = this.props
    update({
      searchParams: {
        warehouseCode: {value: userLoation.value, children: userLoation.value}
      }
    })
  }
  render() {
    const {
      searchParams: {initDate, supplierName, linkOrderCode, orderSource},
      userLoation
    } = this.props
    return (
      <Form className={`${style.form} arrivalPlanSupplier`}>
        <Row type="flex" justify="start">
          <Col span={6}>
            <div className="commonInput">
              <label>预计收货日期：</label>
              <div className="box" style={{marginRight: 0}}>
                <DatePicker
                  onChange={(moment, string) => {
                    this.onInputChange({initDate: moment})
                  }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              data={PROCUREMENT}
              label="预计收货时间段"
              selectItem={{data: orderSource, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({orderSource: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_WDENTRYORDERS_SUPPLIERS}
              label="供应商"
              searchParams={{warehouseCode: userLoation.value}}
              searchParamsKey="supplierCode"
              selectItem={{
                data: supplierName,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({supplierName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({supplierName: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className="commonInput">
              <label>入库单：</label>
              <div className="box">
                <Input
                  value={linkOrderCode}
                  placeholder="请输入(长度不超过10位)"
                  onChange={(e) => {
                    const val = e.target.value
                    console.log('val is', val)
                    if (val.length > 10) {
                      return false
                    }
                    this.onInputChange({linkOrderCode: e.target.value})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end">
              <Button
                type="primary"
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
              <Button
                type="primary"
                className={style.mr10}
                onClick={this.reset}
              >
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



