import * as actionType from './actionType'
import {getData} from 'util'
import {
  API_PRODUCTSTOCK_LOADLIST_PAGE,
  API_PRODUCTSTOCK_EXPORTPRODUCTSTOCK_EXPORT,
  API_PRODUCTSTOCK_EXPORTPRODUCTSTOCKDETAIL_EXPORT,
  API_PRODUCTSTOCK_ORDERLIST,
  API_PRODUCTSTOCK_ORDERLIST_EXPORT,
  API_GET_MENUS_PERMISSIONS_BANS
} from 'api'
//同步
export const update = (object) => ({
  type: actionType.ARRIVALPLAN_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  // if (!reqData.userLoation || !reqData.userLoation.value ) {
  //   return
  // }
  let reqObj = reqData.searchParams

  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      // reqObj[i] = reqObj[i].value
      i === 'productName'
        ? (reqObj[i] = reqObj[i].children)
        : (reqObj[i] = reqObj[i].value)
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.planReceiveDateStart = moment(reqObj.initDate[0]).format(
      'YYYY-MM-DD'
    )
    reqObj.planReceiveDateEnd = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  if (!reqObj.warehouseCode) {
    reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  }
  dispatch({type: actionType.ARRIVALPLAN_UPDATE, data: {tableLoading: true}})
  getData({api: API_PRODUCTSTOCK_LOADLIST_PAGE, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      let {page, size} = reqData.searchParams
      dispatch({
        type: actionType.ARRIVALPLAN_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.ARRIVALPLAN_FILE
      })
    })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_PRODUCTSTOCK_EXPORTPRODUCTSTOCK_EXPORT, ...reqData})
    .then((data) => {
      callBack && callBack()
    })
    .catch((err) => {
      dispatch({
        type: actionType.ARRIVALPLAN_FILE
      })
    })
}

// 明细导出
