// 获取在途库存查询列表
export const handleGetListOM = (reqData) => (dispatch) => {
  dispatch({
    type: actionType.ARRIVALPLAN_UPDATE,
    data: {deliveryTableLoading: true}
  })
  getData({api: API_PRODUCTSTOCK_ORDERLIST, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.ARRIVALPLAN_UPDATE,
        data: {
          deliveryTableLoading: false,
          totalOM: data.result.total,
          pageOM: reqData.searchParams.page,
          sizeOM: reqData.searchParams.size,
          searchResultRecord: data.result.records || []
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.ARRIVALPLAN_FILE
      })
    })
}

// 导出
export const handleExportOM = (reqData, callBack) => (dispatch) => {
  dispatch({
    type: actionType.ARRIVALPLAN_UPDATE,
    data: {modalTuttonLoading: true}
  })
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_PRODUCTSTOCK_ORDERLIST_EXPORT, ...reqData})
    .then((data) => {
      dispatch({
        type: actionType.ARRIVALPLAN_UPDATE,
        data: {
          modalTuttonLoading: false
        }
      })
      callBack && callBack()
    })
    .catch((err) => {
      dispatch({
        type: actionType.ARRIVALPLAN_FILE
      })
    })
}
// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData})
    .then(({data}) => {
      console.log(data)
      // 相应的逻辑判断
      let Power = {}
      data.map((o) => {
        Power[o.menuCode] = true
      })
      dispatch({
        type: actionType.ARRIVALPLAN_UPDATE,
        data: {
          ...Power
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.ARRIVALPLAN_FILE
      })
    })
}



