import React, {PureComponent} from 'react'
import {Modal, Row, Col, Input, Form, message, Button, Table} from 'antd'

import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item
@connect((state) => ({...state.arrivalPlan}), mapDispatchToProps)
class App extends PureComponent {
  constructor() {
    super()
    this.columns = [
      {
        title: '出库单',
        width: 150,
        dataIndex: 'shippedOrderCode',
        key: 'shippedOrderCode'
      },
      {
        title: '业务类型',
        width: 100,
        dataIndex: 'businessTypeDesc',
        key: 'businessTypeDesc'
      },
      {
        title: '在途数量',
        width: 150,
        dataIndex: 'shippedQty',
        key: 'shippedQty'
      }
    ]
  }
  componentDidMount() {
    this.query()
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowQueryModal: false})
  }

  // 重置
  reset = () => {
    const {form} = this.props
    form.resetFields()
  }

  // 查询
  query = () => {
    const {handleGetListOM, form, queryModalId, sizeOM} = this.props
    let inputObj = form.getFieldsValue()
    let reqObj = {...inputObj}
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleGetListOM({
      searchParams: {
        page: 1,
        size: sizeOM,
        productCode: queryModalId,
        ...reqObj
      }
    })
  }

  onChange = (pagination, filters, sorter, extra) => {
    const {
      handleGetListOM,
      searchParams,
      pageOM,
      sizeOM,
      queryModalId
    } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetListOM({
          searchParams: {
            page: pageOM,
            size: sizeOM,
            productCode: queryModalId,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetListOM({
          searchParams: {
            page: pageOM,
            size: sizeOM,
            productCode: queryModalId,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetListOM({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          productCode: queryModalId,
          ...searchParams
        }
      })
    }
  }
  // 导出
  handleExport = () => {
    const {
      handleExportOM,
      searchParams,
      pageOM,
      sizeOM,
      queryModalId,
      form
    } = this.props
    let inputObj = form.getFieldsValue()
    let reqObj = {...inputObj}
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    handleExportOM(
      {
        searchParams: {
          page: pageOM,
          size: sizeOM,
          productCode: queryModalId,
          ...searchParams,
          ...reqObj
        }
      },
      () => {
        message.success('导出成功')
      }
    )
  }
  render() {
    const {
      form: {getFieldDecorator},
      modalTuttonLoading,
      searchResultRecord,
      deliveryTableLoading,
      totalOM,
      pageOM,
      sizeOM,
      queryModalTotalNum
    } = this.props
    let pagination = {
      total: totalOM,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: pageOM,
      pageSize: sizeOM,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16}
      }
    }
    return (
      <Modal
        title={'在途库存查询'}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className="operationModal"
        width={980}
        footer={null}
      >
        <Form autoComplete="off">
          <Row gutter={16}>
            <Col span={8}>
              <FormItem {...formItemLayout} label="出库单">
                {getFieldDecorator('orderCode', {
                  initialValue: ''
                })(<Input />)}
              </FormItem>
            </Col>
            <Col span={16}>
              <Button
                type="primary"
                style={{marginRight: '18px'}}
                onClick={this.query}
              >
                查询
              </Button>
              <Button
                type="primary"
                style={{marginRight: '18px'}}
                onClick={this.reset}
              >
                重置
              </Button>
              <Button
                type="primary"
                loading={modalTuttonLoading}
                onClick={this.handleExport}
              >
                导出
              </Button>
            </Col>
          </Row>
          <p style={{textAlign: 'right'}}>在途总量：{queryModalTotalNum}</p>
        </Form>
        <div className="table-group">
          <Table
            rowKey={(record, index) => index}
            loading={deliveryTableLoading}
            columns={this.columns}
            scroll={{x: 850, y: 400}}
            dataSource={searchResultRecord}
            pagination={pagination}
            onChange={this.onChange}
          />
        </div>
      </Modal>
    )
  }
}
export default Form.create()(App)



