import React, {PureComponent} from 'react'
import {message, Button} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getNowPageId} from '../../global/components/menuData'

@connect(
  (state) => ({...state.arrivalPlan, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {}
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  // 导出
  handleExport = () => {
    const {handleExport, searchParams, page, size, userLoation} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode &&
      (reqObj.splitGroupCode =
        reqObj.splitGroupCode.data &&
        reqObj.splitGroupCode.data.map((o) => o.key)).join(',')
    handleExport(
      {
        searchParams: {
          page,
          size,
          warehouseCode: userLoation.value,
          ...reqObj,
          productName: reqObj.productName ? reqObj.productName.children : ''
        }
      },
      () => {
        message.success('导出成功')
      }
    )
  }
  // 明细导出
  handleExportDet = () => {
    const {handleExportDet, searchParams, page, size, userLoation} = this.props
    console.log(searchParams)
    let reqObj = {...searchParams}
    reqObj.splitGroupCode &&
      (reqObj.splitGroupCode =
        reqObj.splitGroupCode.data &&
        reqObj.splitGroupCode.data.map((o) => o.key)).join(',')
    handleExportDet(
      {
        searchParams: {
          page,
          size,
          warehouseCode: userLoation.value,
          ...reqObj,
          productName: reqObj.productName ? reqObj.productName.children : ''
        }
      },
      () => {
        message.success('导出成功')
      }
    )
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    window.open(downloadData[key].downloadUrl)
  }

  render() {
    const {arivalPlanExport, arivalPlanExportDetail} = this.props
    return (
      <div className={style['table-tool']}>
        {arivalPlanExport && (
          <Button
            type="primary"
            className={style.btn}
            onClick={() => {
              this.handleExport()
            }}
          >
            导出
          </Button>
        )}
        {arivalPlanExportDetail && (
          <Button
            type="primary"
            className={style.btn}
            onClick={() => {
              this.handleExportDet()
            }}
          >
            明细导出
          </Button>
        )}
      </div>
    )
  }
}



