import React, {PureComponent} from 'react'
import {Table, Icon, Popover} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'

@connect(
  (state) => ({...state.arrivalPlan, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {}
    this.columns = [
      {
        title: '日期',
        width: 150,
        dataIndex: 'warehouseCode',
        key: 'warehouseCode'
      },
      {
        title: '预约时间段',
        width: 150,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '供应商数量',
        width: 150,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '入库单数量',
        width: 100,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '商品种类',
        width: 150,
        dataIndex: 'productBarCode',
        key: 'productBarCode'
      },
      {
        title: '商品总金额',
        width: 100,
        dataIndex: 'shipperName',
        key: 'shipperName'
      }
    ]

    this.columns2 = [
      {
        title: '入库单号',
        width: 150,
        dataIndex: 'storeLocationCode',
        key: 'storeLocationCode',
        render: (text, record, index) => (
          <React.Fragment>
            <a
              onClick={() => {
                window.open(`/wms/in-warehouse/inWarehouseList/${text}`)
              }}
            >
              {text}
            </a>
          </React.Fragment>
        )
      },
      {
        title: '供应商',
        width: 150,
        dataIndex: 'storageLocationTypeName',
        key: 'storageLocationTypeName'
      },
      {
        title: '商品种类',
        width: 150,
        dataIndex: 'reservoirAreaCode',
        key: 'reservoirAreaCode'
      },
      {
        title: '订单金额',
        width: 150,
        dataIndex: 'reservoirAreaName',
        key: 'reservoirAreaName'
      }
    ]
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    let reqObj = {...searchParams}
    reqObj.splitGroupCode &&
      (reqObj.splitGroupCode =
        reqObj.splitGroupCode.data &&
        reqObj.splitGroupCode.data.map((o) => o.key)).join(',')
    handleGetList({searchParams: {page, size, ...reqObj}, userLoation})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({
        searchParams: {page: 1, size: newProps.size, ...newProps.searchParams},
        userLoation: newProps.userLoation
      })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode &&
      (reqObj.splitGroupCode =
        reqObj.splitGroupCode.data &&
        reqObj.splitGroupCode.data.map((o) => o.key)).join(',')
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...reqObj,
            isAsc: true,
            columns: sorter.field
          },
          userLoation
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...reqObj,
            isAsc: false,
            columns: sorter.field
          },
          userLoation
        })
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        },
        userLoation
      })
    }
  }
  // 打印条形码
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  render() {
    const {total, page, size, tableLoading, searchResult} = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className="table-group">
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          expandedRowRender={(record) =>
            record.detailList && record.detailList.length ? (
              <Table
                rowKey={(record, index) => index}
                columns={this.columns2}
                dataSource={record.detailList}
                scroll={{x: 'max-content'}}
                pagination={false}
              />
            ) : (
              <p>暂无数据</p>
            )
          }
        />
      </div>
    )
  }
}



